


.ArtistBox {
    background-image: url(/static/media/rock&rollwoman.791af3cb.png);
    background-size: 510px;
    border-radius: 40px;
    height: 100vh;
    display: flexbox;  
}


 .artistHeader {
    background:linear-gradient(180deg,linen, #ED94B6);
    border-radius: 10px;
    align-items: center;
    display: flex;
    justify-content: center;
    font-size: 50px; 

}



.ArtistList{
    background:linear-gradient(180deg,linen, #ED94B6);
    border-radius: 10px;
    max-width: 500px;
    display: flex;
    justify-content: center;

}

.Songs{
    background:linear-gradient(180deg,linen, #ED94B6);
    border-radius: 10px;
    max-width: 500px;
    display: flex;
    justify-content: center;
    padding-top: 50px;
    
}

ul{
    list-style-type: none;

}

button.artist-links {
 
    border-radius: 10px;
    font-size: 20px;
    cursor:pointer;
    justify-content: space-between;
    padding: 0;
    border: none;
    background: none;
    
    
}

button.artist-links:hover{
        background-color: #eca8c2;
        color: white;
        border-radius: 5px;
        transition: all 0.2s ease-out;
}

a.trackname{
    border-radius: 10px;
    font-size: 20px;
    cursor:pointer;
    justify-content: space-between;
    padding: 0;
    border: none;
    background: none;
}

    

